}


label[for^="check"]::before {
  content: " ";
  display: block;
  border-radius: 50%;
  position: absolute;
  width: 1.8rem;
  height: 1.8rem;
  text-align: center;
  transition-duration: var(--trans-time);
  transform: scale(0);
  box-shadow: 0rem 0rem 0.01rem #888888
} 


label img {
  height: 1.8rem;
  width: 1.8rem;
  transition-duration: var(--trans-time);
}



input:checked + label[for^="check"]:before {
  content: "";
  background-color: #F8C741;
  transform: scale(2);
}

input:checked + label img {
  transform: scale(1);
  /* box-shadow: 0 0 5px #333; */
  z-index: -1;
} 

